import React, { Component } from 'react';
import $ from 'jquery';
import './Layout.sass';
import Introduction from '../Introduction/Introduction';
import Portfolio from '../Portfolio/Portfolio';
import Skills from '../Skills/Skills';
import Experience from '../Experience/Experience';
import Footer from '../Footer/Footer';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faBriefcase, faPuzzlePiece, faCogs, faShareAlt } from '@fortawesome/free-solid-svg-icons';

library.add(faHome);
library.add(faBriefcase);
library.add(faPuzzlePiece);
library.add(faCogs);
library.add(faShareAlt);

class Layout extends Component {

    constructor() {
        super();
        this.handleNavItemMouseEnter = this.handleNavItemMouseEnter.bind(this);
        this.handleNavItemMouseOut = this.handleNavItemMouseOut.bind(this);
    }

    componentDidMount() {
        this.setState({
            navBtnWidth: '40px'
        });
    }
    
    handleNavItemMouseEnter(e) {
        e.stopPropagation();
        let a = $(e.target).closest('a');
        a.clearQueue();
        a.css('width', 'auto');
        let aw = a.css('width')
        a.css('width', '40px');
        a.animate({
            width: aw
        }, 300);
        $(e.target).find('.nav-icon').fadeOut();
    }

    handleNavItemMouseOut(e) {
        e.stopPropagation();
        let a = $(e.target).closest('a');
        a.animate({
            width: this.state.navBtnWidth
        }, 500);
        $(e.target).find('.nav-icon').fadeIn();
    }

    render() {
        return (
            <div id="layout-container">
                <nav className="navbar">
                    <ul className="bubbles">
                        <li onMouseEnter={this.handleNavItemMouseEnter} onMouseOut={this.handleNavItemMouseOut}><span class="nav-icon"><FontAwesomeIcon icon={faHome} /></span> <a href="#introduction">Introduction</a></li>
                        <li onMouseEnter={this.handleNavItemMouseEnter} onMouseOut={this.handleNavItemMouseOut}><span class="nav-icon"><FontAwesomeIcon icon={faBriefcase} /></span> <a href="#experience">Experience</a></li>
                        <li onMouseEnter={this.handleNavItemMouseEnter} onMouseOut={this.handleNavItemMouseOut}><span class="nav-icon"><FontAwesomeIcon icon={faPuzzlePiece} /></span> <a href="#portfolio">Portfolio</a></li>
                        <li onMouseEnter={this.handleNavItemMouseEnter} onMouseOut={this.handleNavItemMouseOut}><span class="nav-icon"><FontAwesomeIcon icon={faCogs} /></span> <a href="#skills">Skills</a></li>
                        <li onMouseEnter={this.handleNavItemMouseEnter} onMouseOut={this.handleNavItemMouseOut}><span class="nav-icon"><FontAwesomeIcon icon={faShareAlt} /></span> <a href="#footer">Contact</a></li>
                    </ul>
                </nav>
                <section id="introduction" className="section">
                    <Introduction />
                </section>
                <section id="experience" className="section">
                    <Experience />
                </section>
                <section id="portfolio" className="section">
                    <Portfolio />
                </section>
                <section id="skills" className="section">
                    <Skills />
                </section>
                <section id="footer" className="section">
                    <Footer />
                </section>
            </div>
        );
    }
}

export default Layout;
